.App {
  width: fit-content;
  height: 10rem;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  display: grid;
  place-items: center;
  grid-template-columns: 1fr .8fr;
  margin: 0px auto;
  /* background-color: chartreuse; */
  border: 2px solid #F26E3F;
  border-radius: 2rem;
}

.App_info {
  /* background-color: aquamarine; */
  padding-left: 2rem;
  font-size: .6rem;
  color: #F26E3F;
}

.App_animation {
  padding-left: 2rem;
  /* background-color: burlywood; */
  display: grid;
  place-items: center;
  /* display: flex;
  justify-content: flex-start; */
}

.App_animation iframe {
  border: none;
  width: 10rem;
  height: 10rem;
}
